.main{
  padding-top: 3.875em;
  border-bottom: 1px solid rgba($black,.1);
  &__grid{
    display: grid;
    grid-template-columns: 29.5em 34.625em;
    justify-content: space-between;
    gap: 2.75em;
    margin-top: 1.875em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      right: 37.75em;
      height: 100%;
      width: 1px;
      background-color: rgba($black,.1);
    }
  }
  &__num{
    width: auto;
    height: 5.6875em;
    display: block;
    margin-bottom: 1.5em;
  }
  &__col{
    padding-bottom: 2.375em;
  }
  &__fields{
    margin-top: 2.125em;
    @include grid100gap(.8125em);
  }
  &__check{
    margin-top: 1.375em;
  }
  &__btn{
    margin-top: 1.375em;
    height: 5.0625em;
    width: 100%;
    border-radius: .3125em;
    span{
      font-size: 1.25em;
    }
  }
  &__bottom{
    display: flex;
    margin-top: 1.75em;
  }
  &__how-pay{
    height: 3.375em;
    border-radius: 31.25em;
    font-weight: 700;
    &:not(:hover){
      color: $gray;
    }
    &:hover{
      rect{
        fill: transparent;
      }
      .path-fill{
        fill: #fff;
      }
      .stroke-fill{
        stroke: #fff;
      }
    }
    span{
      font-size: 1.125em;
    }
  }
}

.pay-ways{
  margin-top: 1.25em;
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
  gap: 1.25em 1em;
}

.pay-way{
  position: relative;
  padding: 1em;
  background: #FFFFFF;
  box-shadow: 0 .75em 3.5em rgba(0, 0, 0, 0.11);
  border-radius: .875em;
  min-height: 7.25em;
  display: flex;
  flex-direction: column;
  align-items: center;
  label,
  input{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
  }
  input{
    z-index: 3;
    cursor: pointer;
    &:checked~label{
      opacity: 1;
    }
  }
  label{
    pointer-events: none;
    z-index: 2;
    border-radius: inherit;
    border: 1px solid $primary;
    transition: all .3s;
    &:before{
      content: '';
      position: absolute;
      top: -.375em;
      right: -.625em;
      width: 1.5em;
      height: 1.5em;
      background-image: url(../img/check2.svg);
      @include bgImgSize(100%);
    }
  }
  &__img{
    height: 3.5em;
    width: 6.25em;
    @include center;
    margin-bottom: .75em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-weight: 700;
    font-size: .9375em;
    line-height: 120%;
    color: $dark;
  }
}

.pay-detail{
  margin-top: 3.125em;
  @include grid100gap(1em);
  &__row{
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #545454;
    &--last{
      padding-top: 1em;
      border-top: 1px solid rgba($dark,.2);
      font-weight: 700;
      .pay-detail__title,
      .pay-detail__desc{
        font-size: 1.5em;
        line-height: 121%;
      }
    }
  }
  &__title,
  &__desc{
    font-size: 1.125em;
    line-height: 117%;
  }
}

.advantages{
  padding: 5em 0 3em;
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 1.25em 2.5em;
  }
  &__items{
    @include grid100gap(.625em);
  }
}

.advantages-info{
  position: sticky;
  top: 1em;
  @include grid100gap(2.1875em);
  .text45{
    max-width: 10em;
  }
}

._card{
  background: #FFFFFF;
  box-shadow: 0 .6875em 3.4375em rgba(0, 0, 0, 0.12);
  border-radius: .6875em;
  padding: 1.25em;
}

.advantage-card{
  display: grid;
  grid-template-columns: 2.125em calc(100% - 3.125em);
  gap: 1em;
  &__icon{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.625em);
  }
  &__title{
    line-height: 140%;
  }
}

.review{
  padding: 3em 0 2.75em;
  &__inner{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: calc(100% - 25em) 22.5em;
    gap: 1.5625em 2.5em;
  }
  &__items{
    @include grid100gap(1.25em);
  }
}

.review-card{
  &._comment{
    .review-card__add{
      display: none;
    }
    .review-card__mess{
      display: block;
    }
  }
  &__wrap{
    display: flex;
  }
  &__img{
    width: 3.3125em;
    height: 3.3125em;
    flex-shrink: 0;
    margin-right: 1.125em;
    img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.9375em);
  }
  &__title,
  &__desc{
    line-height: 140%;
  }
  &__comment{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-right{
      flex-shrink: 0;
      margin-left: 1em;
    }
    &-left{
      flex-grow: 1;
    }
  }
  &__add{
    text-decoration-line: underline;
    font-size: .9375em;
    line-height: 120%;
    color: $blue;
    &:hover{
      text-decoration: none;
    }
  }
  &__like{
    display: flex;
    align-items: center;
    img{
      width: .625em;
      margin-right: .25em;
    }
  }
  &__mess{
    position: relative;
    display: none;
    margin-top: -.625em;
    input{
      width: 100%;
      font-size: .9375em;
      line-height: 120%;
      padding: .67em 2em .67em 0;
      height: unset;
      border: none;
      border-bottom: 1px solid rgba($black,.32);
      color: $black;
      &::placeholder{
        color: #B4B4B4;
      }
    }
  }
  &__send{
    position: absolute;
    width: 1.1875em;
    top: 50%;
    right: .625em;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    img{
      width: 100%;
    }
  }
  &__response{
    margin-top: .9375em;
    .review-card__content{
      gap: .5em;
    }
  }
}

.review-sidebar{
  position: sticky;
  top: 1em;
  &__text{
    opacity: .8;
  }
  &__nav{
    margin-top: 1.5625em;
    @include grid100gap(1.0625em);
  }
  &__btn{
    height: 3.5em;
    span{
      font-size: 1.25em;
    }
  }
}

.review-platforms{
  padding: .625em;
  margin-top: 1.0625em;
  background: rgba(239, 239, 239, 0.55);
  border-radius: .6875em;
  text-align: center;
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1em;
  }
  &__btn{
    width: 2.25em;
    height: 2.25em;
    background: #EEEEEE;
    border-radius: .1875em;
    @include center;
    margin: 0 .25em;
    &:hover{
      background: darken(#eee,10%);
    }
    img{
      width: 1.5em;
      display: block;
    }
  }
}

.faq{
  padding: 2.75em 0 4.0625em;
  &__items{
    margin-top: 2.1875em;
    @include grid100gap(1.25em);
  }
}

.qa-card{
  &._active{
    .qa-card__btn{
      span{
        transform: rotate(-45deg);
      }
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:hover{
      cursor: pointer;
    }
  }
  &__btn{
    width: 2.0625em;
    height: 2.75em;
    @include center;
    font-weight: 700;
    span{
      font-size: 1.25em;
      line-height: 125%;
      transition: all .3s;
    }
    &:hover{
      color: $primary;
    }
  }
  &__hidden{
    display: none;
    padding-top: .625em;
  }
}

.new-question{
  display: flex;
  align-items: center;
  margin-top: 2.1875em;
  &__icon{
    width: 2em;
    flex-shrink: 0;
    margin-right: .9375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__btn{
    margin-left: auto;
    flex-shrink: 0;
  }
}

.blog{
  padding: 4.0625em 0 5.75em;
  overflow: hidden;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  &__slider{
    margin-top: 2.5em;
  }
  .swiper{
    &-container{
      width: calc(100% + 2.5em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: calc(100% / 3);
      height: auto;
      padding-right: 2.5em;
    }
    &-button{
      position: static;
      margin: 0 0 0 .625em;
      background: #FFFFFF;
      box-shadow: 0 .6875em 3.4375em rgba(0, 0, 0, 0.12);
      border-radius: .3125em;
      width: 3.75em;
      height: 2.8125em;
      svg{
        width: 1.875em;
        height: auto;
      }
      &:hover{
        background: $primary;
        path{
          fill: #fff;
        }
      }
    }
  }
  &__bottom{
    display: flex;
    margin-top: 2.125em;
  }
  &__btn{
    width: 22em;
    max-width: 100%;
    height: 3.5em;
    span{
      font-size: 1.25em;
    }
  }
}

.blog-card{
  height: 100%;
  display: flex;
  flex-direction: column;

  &__img{
    display: block;
    @include imgCover(73%);
    img{
      border-radius: .375em .375em 0 0;
    }
  }
  &__content{
    border-radius: 0 0 .375em .375em;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__info{
    @include grid100gap(.875em);
  }
  &__title{
    line-height: 125%;
  }
  &__desc{
    line-height: 130%;
  }
  &__nav{
    padding-top: .875em;
    margin-top: auto;
    display: flex;
  }
}

.about{
  padding: 5em 0;
  &__text{
    max-width: 60em;
  }
}



@media screen and (max-width: 992px){
  .main{
    padding: 1.375em 0 3.25em;
    border: none;
    &__grid{
      grid-template-columns: 100%;
      gap: 2.75em;
      position: relative;
      padding-bottom: 7.5em;
      &:before{
        display: none;
      }
    }
    &__col{
      padding: 0;
    }
    &__num{
      height: 3.375em;
      margin-bottom: 1em;
    }
    &__fields{
      margin-top: 1.125em;
    }
    &__btn{
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
    }
  }

  .pay-ways{
    margin-top: .625em;
    grid-template-columns: repeat(2,calc(50% - .125em));
    gap: .25em;
  }

  .pay-way{
    min-height: 6.25em;
    padding: .5em;
  }

  .pay-detail{
    margin-top: 1.75em;
    &__row{
      &--last{
        .pay-detail__title,
        .pay-detail__desc{
          font-size: 1.25em;
          line-height: 120%;
        }
      }
    }
    &__title,
    &__desc{
      font-size: .875em;
      line-height: 121%;
    }
  }

  .advantages{
    padding: 1.1875em 0 4.6875em;
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .advantages-info{
    gap: 1.25em;
    .text45{
      max-width: 100%;
    }
  }

  .advantage-card{
    &__title{
      font-size: 1em;
    }
  }

  .review{
    padding: 4.6875em 0 2.125em;
    &__inner{
      margin-top: .875em;
      grid-template-columns: 100%;
      padding-top: 8.75em;
      position: relative;
    }
  }

  .review-sidebar{
    position: static;
    &__text{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      font-size: .9375em;
      line-height: 140%;
    }
    &__nav{
      gap: .625em;
      margin: 0;
    }
    &__btn{
      height: 3.125em;
      span{
        font-size: .9375em;
      }
    }
  }

  .review-platforms{
    margin-top: .625em;
  }

  .faq{
    padding: 2.125em 0 2.25em;
    &__items{
      gap: .9375em;
      margin-top: 1.625em;
    }
  }

  .qa-card{
    &__title{
      font-size: 1em;
      line-height: 130%;
    }
  }

  .blog{
    padding: 2.25em 0 4.5em;
    .text45{
      font-size: 1.5625em;
    }
    &__slider{
      margin-top: 1.375em;
    }
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 20em;
        padding-right: 1.25em;
      }
      &-button{
        width: 2.8125em;
        height: 2.8125em;
      }
    }
    &__bottom{
      margin-top: 1.25em;
    }
  }

  .blog-card{
    &__img{
      padding-bottom: 80%;
    }
  }

  .about{
    padding: 1.1875em 0;
    .text45{
      font-size: 1.5625em;
    }
    &__text{
      margin-top: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__how-pay{
      width: 100%;
    }
  }

  .review-card{
    &__img{
      width: 2.125em;
      height: 2.125em;
    }
    &__content{
      gap: .625em;
    }
    &__title{
      font-size: 1em;
      line-height: 119%;
    }
    &__response{
      margin-top: 1.25em;
    }
    &__comment{
      position: relative;
      &-right{
        margin: 0;
      }
    }
    &__like{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 0;
    }
    &__add{
      font-size: .875em;
    }
    &__send{
      right: 2.25em;
      width: .75em;
    }
    &__mess{
      margin-top: 0;
      input{
        font-size: .75em;
        line-height: 117%;
        padding: 1em 4.5em 1em 0;
      }
    }
  }

  .new-question{
    flex-direction: column;
    text-align: center;
    margin-top: 2.75em;
    &__icon{
      margin: 0 0 .9375em;
    }
    &__btn{
      margin: .9375em auto 0;
      height: 2.375em;
      span{
        font-size: .9375em;
      }
    }
  }
}
